import { createRouter, createWebHistory } from 'vue-router'
// all the different pages, complete with their components
import HomeView from '../views/IndexPage.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/Common-Questions',
    name: 'common-questions',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/QuestionsPage.vue')
    }
  },
  {
    path: '/Rally-Ranch',
    name: 'rally-ranch',

    component: function () {
      return import('../views/RallyRanchPage.vue')
    }
  },
  {
    path: '/Set-Making',
    name: 'set-making',

    component: function () {
      return import('../views/SetMakingPage.vue')
    }
  },
  {
    path: '/Teambuilding',
    name: 'teambuilding',

    component: function () {
      return import('../views/TeambuildingPage.vue')
    }
  },
  {
    path: '/Resources',
    name: 'resources',

    component: function () {
      return import('../views/ResourcesPage.vue')
    }
  },
  {
    path: '/Credits',
    name: 'credits',

    component: function () {
      return import('../views/CreditsPage.vue')
    }
  },

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
